	module.exports = {"data":{"markdownRemark":{"html":"<p>Today, I sit down with Hadiya Nuriddin, an instructional design and training veteran, who now owns her own consultancy, Focus Learning Solutions.</p>\n<iframe src=\"https://simplecast.com/e/49389?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>I was interested in having Hadiya on when I heard about her new venture FreshEye Reviews, where she’ll be focusing on simplifying the quality control process for organizations. In this episode, we talk about quality control vs. quality assurance and how we should implement these processes as a non-negotiable in our creation process. As always, get the shownotes are available on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"November 17, 2016","path":"/blog/dearid-interview-with-hadiya-nuriddin","title":"#DearID: Interview with Hadiya Nuriddin","tags":["design","instructional design","quality assurance","quality control","review"],"categories":["Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

